@use 'base/mixins' as m;
@use 'base/variables' as v;

.galeria{
    padding: 5rem 0;    
    h3{
       text-align: center; 
    }
}
.galeria-imagenes{
    cursor:pointer;
    @include m.reset-ul;
    @include m.grid(true,2,3rem);
    @include m.mediaQuery(v.$phone){
        @include m.grid(true,3,3rem);
    }
    @include m.mediaQuery(v.$tablet){
        @include m.grid(true,4,3rem);
    }
    @include m.mediaQuery(v.$desktop){
        @include m.grid(true,6,3rem);
    }
}
.overlay{
    position: fixed;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    
    width: 100%;
    height: 100%;

    display: grid;
    place-items: center;

    background-color: rgb(0 0 0 / .5);

    picture{
        display: block;
        max-width: 80rem;
    }

    .btn-cerrar{
        width: 6rem;
        height: 6rem;
        border-radius: 50%;
        background-color: v.$azulOscuro;
        font-size: 3rem;
        color: v.$blanco;
        display: grid;
        place-items: center;
        font-weight: bold;
        margin: 0;
        &:hover{
            cursor: pointer;
            background-color: v.$negro;
        }
    }
}