@use 'base/variables' as v;
@use 'base/mixins' as m;


.header{
    background-color: v.$negro;
    &-fijo{
        @include m.mediaQuery(v.$tablet) {
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;    
        }
    }
    
    h1{
        color: v.$blanco;
        text-align: center;margin: 2rem;
        @include m.mediaQuery(v.$tablet) {
            margin: 0;    
        }
    }
    &__contenido{
        padding: 2rem;
        @include m.mediaQuery(v.$tablet) {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
}


.navegacion-principal{
    @include m.mediaQuery(v.$tablet) {
        display: flex;
        gap: 2rem;
    }
    a{
        color: v.$blanco;
        font-size: 2.4rem;
        display: block;
        text-align: center;
        margin-bottom: 1rem;
        @include m.mediaQuery(v.$tablet) {
            margin: 0;
        }
        &:hover{
            color: v.$amarillo;
        }
        &.nav-color{
            color: v.$amarillo;
            
        }
    }

}


