@use 'base/mixins' as m;
@use 'base/variables' as v;

.sobre-festival{
    padding: 5rem 0;
    @include m.mediaQuery(v.$tablet) {
        @include m.grid(true,2,2rem);    
    }
}
.contenido-festival{
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    h2{
        margin-bottom: 2rem;
    }
    .fecha{
        color: v.$azulOscuro;
        font-weight: bold;
    }
}
