@use 'base/mixins' as m;
@use 'base/variables' as v;


.video{
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;
    .overlay{
        position: absolute;
        background: rgb(24,0,101);
        background: linear-gradient(270deg, rgba(24,0,101,0.4778244061296394) 17%, rgba(0,0,0,0.2873482156534489) 100%);
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
    }


    video{
        width: auto;
        display: block;
        @include m.mediaQuery(v.$desktop) {
            width: 100%;
        }
    }
}

.contenedor-video{
    z-index: 10;
    h2{
        color: v.$blanco;
        font-size: 2rem;
        margin: 0;
        @include m.mediaQuery(v.$phoneMini) {
            font-size: 4rem;
        }
        @include m.mediaQuery(v.$tablet) {    
            font-size: 7rem;
        }
    }
    p{
        color: v.$blanco;
        background-color: v.$negro;
        display: inline-block;
        padding: 1rem 2rem;
        font-weight: bold;
        font-size: 2rem;
    }
}