@use 'base/mixins' as m;
@use 'base/variables' as v;

.lineup{
    // background: rgb(61,12,67);
    // background: linear-gradient(86deg, rgba(61,12,67,1) 0%, rgba(50,37,77,1) 45%, rgba(0,5,80,1) 100%); 
    background-color: v.$negro;
    padding: 5rem 0;

    h3,p{
        color: v.$blanco;
        text-align: center;
    }
    li{
        color: v.$blanco;
    }
    .dia{
        text-transform: uppercase;
        &:first-of-type{
            margin-top: 0;
        }
        margin: 5rem 0;
    }
}
.escenarios-contenedor{
    @include m.mediaQuery(v.$tablet) {
        @include m.grid(true,2,5rem)
}
}
.escenario{
    padding-top: 2rem;
    &:first-of-type{
        margin-bottom: 3rem;
        @include m.mediaQuery(v.$tablet){
            margin-bottom: 0;
        }
    }
    
    .escenario-nombre{
        &:after{
            content: "";
            display: block;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center;
            width: 20rem;
            height: 10rem;
            margin: 2rem auto 0 auto;
        }
    }
}
.edm .escenario-nombre:after{
    background-image: url("../img/icono_edm.png");
}
.rock .escenario-nombre:after{
    background-image: url("../img/icono_rock.png");
}
.bg-azulOscuro{
    background-color: v.$azulOscuro;
    li:nth-child(odd){
        background-color: darken(v.$azulOscuro, 10%);
    }
}
.bg-rojo{
    background-color: v.$rojo;
    li:nth-child(odd){
        background-color: darken(v.$rojo, 10%);
    }
}

.calendario{
    @include m.reset-ul;
    li{
        padding: 3rem;
        font-weight: 700;
        span{
            font-weight: 400;
        }
    }
}
