@use 'base/mixins' as m;
@use 'base/variables' as v;

.boletos-section{
    color: v.$blanco;
    background-color: v.$negro;
    padding: 5rem 0;
    h3{
        text-align: center;
        font-weight: bold;
    }
}

.boletos-contenido{
    @include m.mediaQuery(v.$tablet) {
        @include m.grid(true,2,5rem);
    }
}


.boletos{
    text-align: center;
    p{
        color: v.$blanco;
    }
    header{
        background-color: v.$azulOscuro;
    }
    footer{
        background-color: v.$rojo;
    }
    header, footer{
        padding: 1rem;
    }
    &-descripcion{
        background-color: v.$blanco;
        color: v.$negro;
        @include m.reset-ul;
        li{
            padding: 1rem;
            text-align: left;
        }
    }
}
