@mixin mediaQuery($screen) {
    @media (min-width: $screen) {
        @content; 
    }
}
@mixin grid($type,$value,$gap){
    display: grid;
    @if $type{
        grid-template-columns: repeat($value,1fr);
    } @else{
        grid-template-columns: repeat($value,1fr);
    }
    gap: $gap;
}
@mixin reset-ul{
    padding: 0;
    margin: 0;
    list-style: none;
}
    